<template>
    <div>
        <h2>分类列表</h2>
        <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column
                prop="_id"
                label="ID"
                width="270">
            </el-table-column>
            <el-table-column
                prop="name"
                label="分类名称"
                width="180">
            </el-table-column>
             <el-table-column
                prop="icon"
                label="图标"
                width="180">
                <template slot-scope="scope">
                   <img :src="scope.row.icon" style="height:3rem">
                </template>
            </el-table-column>
            <el-table-column
                fixed="right"
                label="操作"
                width="100">
                <template slot-scope="scope">
                    <el-button @click="$router.push(`/items/edit/${scope.row._id}`)" type="text" size="small">编辑</el-button>
                    <el-button @click="move(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: "ItemsList",
        inject: ['reload'],
        data(){
            return{
                tableData:[]
            }
        },
       created() {
           this.getData();
       },
        methods: {
            getData(){
                // console.log('获取')
                this.$http.get('/rest/items').then(res=>{
                    // console.log(res);
                    this.tableData = res.data;
                })
                // this.reload();
            },
            move(row){  //删除某一项
                this.$confirm(`此操作将永久删除${row.name}, 是否继续?`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                    }).then(() => {
                        this.$http.delete(`/rest/items/${row._id}`).then(res=>{
                            this.reload(); // 更新页面
                        })
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                });
            }
        },
    }
</script>

<style lang="stylus" scoped ref="stylesheet/stylus">

</style>